import React, {useEffect} from 'react';
import {Col, Container, Row} from "react-bootstrap";
import "./about.css"
import SectionTitle from "../gui/SectionTitle";
import {useInView} from "react-intersection-observer";

const Education = React.forwardRef((props, ref) => {
    const [headerRef, headerInView] = useInView();
    const icons = document.getElementsByClassName("about-icon");
    useEffect(() => {
        if (icons !== null) {
            for (let i = 0; i < icons.length; i++) {
                if (headerInView) icons.item(i).style.fill = "black";
                else icons.item(i).style.fill = "white"
            }
        }
    }, [headerInView, icons]);
    return (
        <Container className="w-100 h-100 p-5" ref={ref}>
            <Row ref={headerRef}>
                <Col>
                    <SectionTitle title="Education" textColor="text-dark"/>
                </Col>
            </Row>
            <Row>
                <Col className="text-dark">
                    <h5 className="text-dark">University of Saint Louis, Tuguegarao, Cagayan Valley</h5>
                    <h6 className="text-dark">Bachelor of Science in Computer Science</h6>
                    <h6 className="text-dark">April 2017</h6>
                    <ul className="text-dark text-left pt-3">
                        <li className="text-dark"><p className="text-dark">Managed team members of thesis and allocated
                            resources to fit needs of
                            software development, documents and research</p>
                        </li>
                        <li className="text-dark">
                            <p className="text-dark">Develop a <a href="https://github.com/ianparcs/location-finder">Navigation
                                System</a> which navigate user from current location to target location for local school
                                universities.</p>
                        </li>
                    </ul>
                </Col>
            </Row>
        </Container>
    );
});
export default Education;