import React from 'react';
import {Col, Container, Row} from "react-bootstrap";
import SectionTitle from "../gui/SectionTitle";
import "./workexp.css"

const WorkExperience = React.forwardRef((props, ref) => {

    return (
        <Container id="experience-container" className="pt-4 pb-4 pr-5 pl-5 d-flex flex-column justify-content-center"
                   ref={ref}>
            <Row className="pt-3">
                <Col>
                    <SectionTitle title="Work Experience" textColor="text-white"/>
                </Col>
            </Row>
            <Row>
                <Col className="pt-4 col-12 col-lg-4 d-flex flex-column justify-content-center">
                    <div>
                        <h4>Jr. Software Developer</h4>
                        <h5>Philippine General Hospital</h5>
                        <h6>MARCH 2019  MARCH 2020</h6>
                    </div>
                </Col>
                <Col className="col-12 col-lg-8">
                    <ul className="text-left pt-3">
                        <li><p>Delivered a price portal management module for medical drug items.</p></li>
                        <li><p>Collaborated with development and senior developer on timelines for new version
                            releases.</p></li>
                        <li><p>Identify the root cause of unstable bug from the feedback of users, fix the underlying
                            issues that resulted a satisfied users.</p></li>
                    </ul>
                </Col>
            </Row>
            <Row>
                <Col className="pt-4 col-12 col-lg-4 d-flex flex-column justify-content-center">
                    <h4>Jr. Software Developer</h4>
                    <h5>Codegum Incorporated</h5>
                    <h6>OCTOBER 2017  NOVEMBER 2018</h6>
                </Col>
                <Col className="col-12 col-lg-8">
                    <ul className="text-left pt-3">
                        <li><p>Authenticated user AWS credentials to make request from Amazon S3.</p></li>
                        <li><p>Added real time notification feature using Firebase Cloud Messaging.</p></li>
                        <li><p>Implemented Amazon S3 for the project to save and retrieve data from cloud.</p></li>
                        <li><p>Integrated Facebook login function using Facebook API for android. User can login via
                            Facebook account.</p></li>
                        <li><p>Integrated Google Sign-In function using Google API for android. User can login via
                            Google account.</p></li>
                        <li><p>Provide technical leadership, training, guidance, feedback, and coaching to internships
                            and new developers.</p></li>
                        <li><p>Suggest and implement process improvement plans, tracks progress, and achieves
                            objectives.</p></li>
                    </ul>
                </Col>

            </Row>
        </Container>
    );
});
export default WorkExperience;